<template xmlns:v-lazy="http://www.w3.org/1999/xhtml">
    <div class="dynamic-detail">
        <navigate title="详情" position="absolute"/>
        <div class="scroll-wrapper">
            <div class="dy-container">
                <!-- 头部信息 -->
                <div class="dynamic-head">
                    <!-- 发布人 -->
                    <div class="source">
                        <mu-avatar size="50">
                            <img v-lazy="AvatarImage($images + detail.portrait, detail.alias)">
                        </mu-avatar>
                        <div class="info">
                            <div class="nickname">{{detail.alias}}</div>
                            <div class="time">{{detail.createTime}}</div>
                        </div>
                    </div>
                    <!-- 发布人 -->
                    <!-- 动态圈内容 -->
                    <div class="msg" v-html="detail.content">{{detail.content}}</div>
                    <!-- 动态圈内容 -->
                    <!-- 照片框 -->
                    <div class="picture-box">
                        <div class="picture" v-for="(item, index) in detail.images"
                             :class="picture"
                             v-if="item"
                             v-lazy:background-image="item"
                             @click="handlerPicture(detail.images, index)"></div>
                    </div>
                    <!-- 照片框 -->
                    <!-- 点赞列表 -->
                    <div class="liker" v-if="fabulous.length">
                        <mu-icon value="thumb_up" style="margin-top: 1.8px; margin-bottom: 1px" size="14"/>
                        <div v-html="fabulousString"></div>
                    </div>
                    <!-- 点赞列表 -->
                </div>
                <!-- 头部信息 -->
                <div style="height: 15px;"></div>
                <!-- 评论 -->
                <div class="interact" v-if="commentList.length">
                    <div class="comment">
                        <div class="comment-item border-1px-b" v-for="(item, index) in commentList" :key="index">
                            <mu-avatar size="50">
                                <img v-lazy="AvatarImage($images + item.portrait, detail.alias)">
                            </mu-avatar>
                            <div class="message">
                                <div class="nickname">{{item.alias}}</div>
                                <div class="time">
                                    <span style="margin-right: 0.5em">第{{index + 1}}楼</span>
                                    <span>{{item.createTime}}</span>
                                </div>
                                <div class="comment-content">{{item.content}}</div>
                                <div class="reply border-1px-t" v-for="rItem in item.subComments">
                                    <div style="margin-bottom: 8px">
                                        <span class="reply-name">{{rItem.alias}}</span>
                                        <span class="time">{{rItem.createTime}}</span>
                                    </div>
                                    <div class="reply-content">
                                        {{rItem.content}}
                                    </div>
                                </div>
                            </div>
                            <mu-menu class="more" popover-class="more-popover" placement="right"
                                     :open.sync="item.MoreOpen"
                                     @open="handlerMoreOpen">
                                <mu-icon size="22" value="more_horiz" color="#3899D7"/>
                                <mu-list class="more-list" slot="content">
                                    <mu-list-item @click.native="handlerMoreItem(item)">
                                        <mu-list-item-title>回复</mu-list-item-title>
                                    </mu-list-item>
                                </mu-list>
                            </mu-menu>
                        </div>
                    </div>
                </div>
                <!-- 评论 -->
            </div>
        </div>

        <!-- 底部栏 -->
        <transition-group tag="div" class="dy-bottom-wrapper" name="fade-bottom">
            <div class="button-wrapper" key="b" v-if="visible">
                <mu-ripple class="item" @click="handlerComment">
                    <mu-icon value="speaker_notes" size="22"></mu-icon>
                    <span style="margin-left: .5em">评论</span>
                </mu-ripple>
                <div class="line border-1px-l"></div>
                <mu-ripple class="item"
                           :class="{'my-like': detail.isFabulous}"
                           v-loading="loading.fabulous"
                           data-mu-loading-size="24"
                           @click="handlerFabulous">
                    <mu-icon value="thumb_up" size="22"></mu-icon>
                    <span style="margin-left: .5em">{{detail.isFabulous ? '已赞' : '赞'}}</span>
                </mu-ripple>
            </div>
            <form class="input-box-wrapper" action="javascript:" key="i" @submit="onSubmit" v-else>
                <field v-model="content" maxlength="120" ref="field"
                       :placeholder="placeholder"
                       @blur="visible = true"/>
            </form>
        </transition-group>
        <!-- 底部栏 -->
    </div>
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import Field from 'vant/lib/field'
    import 'vant/lib/field/style'
    import ImagePreview from "vant/lib/image-preview"
    import "vant/lib/image-preview/style"
    import Shelter from "../../base/shelter"
    import {
        queryCommentList,
        queryFabulousAll,
        saveFabulous,
        deleteFabulous,
        saveComment,
        queryCircleMessageWithUF
    } from "../../api/dynamic"
    import {userMixins} from "../../common/mixins"

    export default {
        name: "dynamic-detail",
        mixins: [userMixins],
        data() {
            return {
                visible: true,
                replyTo: null,
                content: "",
                detail: {
                    isFabulous: false
                },
                commentList: [],
                fabulous: [],
                params: {
                    pageNumber: 0,
                    pageSize: 20
                },
                loading: {
                    fabulous: false,
                    comment: false
                }
            }
        },
        props: {
            id: {
                type: String,
                default: ""
            }
        },
        computed: {
            picture() {
                if (this.detail.images) {
                    return this.detail.images.length > 4
                        ? "picture-max"
                        : "picture-" + this.detail.images.length
                }

                return ""
            },
            fabulousString() {
                return (this.fabulous.map(item => {
                    return `<span class="fabulous">${item}</span>`
                }).join("、") + "赞过")
            },
            placeholder() {
                return this.replyTo ? `回复 ${this.replyTo.alias}: ` : `评论`
            }
        },
        activated() {
            this.commentList = []
            this.fabulous = []

            this.detail = this.$route.params

            this.queryCommentList()
            this.queryFabulousAll()
            this.queryCircleMessageWithUF()
        },
        methods: {
            queryCircleMessageWithUF() {
                queryCircleMessageWithUF(this.id).then(res => {
                    console.log(res)
                })
            },
            // 点击图片，打开图片查看器
            handlerPicture(images, index) {
                ImagePreview({images, startPosition: index})
            },
            /***
             * 点击发送评论
             */
            onSubmit() {
                this.saveComment()

                return false
            },
            /***
             * 获取评论列表
             */
            queryCommentList() {
                this.params.msgId = this.id

                queryCommentList(this.params).then(res => {
                    this.commentList = res
                })
            },
            /***
             * 获取所有点赞人
             */
            queryFabulousAll() {
                queryFabulousAll({msgId: this.id}).then(res => {
                    this.fabulous = res
                })
            },
            // 点击更多
            handlerMoreOpen() {
                Shelter.show({
                    zIndex: 20000
                })
            },
            // 点赞 or 取消点赞
            handlerFabulous() {
                if (this.loading.fabulous) {
                    return
                }

                this.loading.fabulous = true

                if (this.detail.isFabulous) {
                    deleteFabulous({msgId: this.detail.id}).then(res => {
                        this.detail.isFabulous = !this.detail.isFabulous

                        this.queryFabulousAll()

                        this.$emit('refresh')

                        this.loading.fabulous = false
                    })
                } else {
                    saveFabulous({msgId: this.detail.id}).then(res => {
                        this.detail.isFabulous = !this.detail.isFabulous

                        this.queryFabulousAll()

                        this.$emit('refresh')

                        this.loading.fabulous = false
                    })
                }
            },
            /***
             * 点击评论按钮
             */
            handlerComment() {
                this.visible = false

                this.replyTo = null
            },
            // 评论
            saveComment() {
                if (!this.content || this.loading.comment) {
                    return
                }

                this.loading.comment = true

                const data = {
                    msgId: this.id,
                    content: this.content,
                    status: "020002"
                }

                if (this.replyTo) {
                    data.commentId = this.replyTo.id

                    console.log(`你回复了 ${this.replyTo.alias}: ${this.content}`)
                } else {
                    console.log(`你发表了评论: ${this.content}`)
                }

                saveComment(data).then(res => {
                    this.content = ""

                    this.queryCommentList()

                    this.$emit('refresh')

                    this.visible = true

                    this.loading.comment = false
                })
            },
            handlerMoreItem(item) {
                this.replyTo = item

                item.MoreOpen = !item.MoreOpen

                Shelter.close()

                this.visible = false
            }
        },
        watch: {
            visible(value) {
                if (!value) {
                    this.$nextTick(() => {
                        this.$refs.field
                        && this.$refs.field.$refs
                        && this.$refs.field.$refs.input
                        && this.$refs.field.$refs.input.focus
                        && this.$refs.field.$refs.input.focus()
                    })
                }
            }
        },
        components: {
            Navigate,
            Field
        }
    }
</script>

<style lang="stylus" scoped>
    @import '../../common/stylus/variable.styl'
    @import '../../common/stylus/mixins.styl'

    .dynamic-detail
        padding 46px 0 44px
        background-color #efefef
        overflow hidden
        .scroll-wrapper
            width 100%
            height 100%
            .dy-container
                display flex
                flex-direction column
                min-height 100%
                .dynamic-head
                    padding 15px
                    background-color #fff
                    .source
                        display flex
                        align-items center
                        margin-bottom 10px
                        > .mu-avatar
                            margin-right 15px
                        .info
                            .nickname
                                margin-bottom 5px
                                font-size $font-size-ls
                                color #333
                            .time
                                font-size $font-size-ms
                                color #aaa
                    .msg
                        margin-bottom 10px
                        line-height 1.56425
                        color #333333
                    .picture-box
                        display flex
                        flex-wrap wrap
                        .picture
                            height 0
                            background-position center center
                            background-repeat no-repeat
                            background-size cover
                        .picture-1
                            padding-top 100%
                            width 100%
                        .picture-2
                            margin-right 4px
                            padding-top calc(50% - 2px)
                            width calc(50% - 2px)
                            &:last-child
                                margin-right 0
                        .picture-3
                            margin-right 3px
                            padding-top calc(33% - 2px)
                            width calc(33% - 2px)
                            &:last-child
                                margin-right 0
                        .picture-4
                            margin-right 4px
                            margin-bottom 4px
                            padding-top calc(50% - 2px)
                            width calc(50% - 2px)
                            &:nth-child(2n)
                                margin-right 0
                            &:nth-child(n + 2)
                                margin-bottom 0
                        .picture-max
                            margin-right 3px
                            margin-bottom 3px
                            padding-top calc(33% - 2px)
                            width calc(33% - 2px)
                            &:nth-child(3n)
                                margin-right 0
                            &:nth-child(n + 7)
                                margin-bottom 0
                    .liker
                        display flex
                        padding 5px 0
                        line-height 1.47625
                        font-size $font-size-ms
                        >>> .fabulous
                            margin-right 0.5em
                            color #2196f3
                        .mu-icon
                            margin-right 0.5em
                            color #2196f3
                .interact
                    flex 1
                    background-color #fff
                    .comment-item
                        position relative
                        display flex
                        padding 10px 15px
                        > .mu-avatar
                            margin-right 15px
                            min-width 50px
                            position absolute
                        .message
                            color #333333
                            padding-left 70px
                            .nickname
                                margin-bottom 6px
                                font-size $font-size-ml
                                color #3674B3
                            .time
                                margin-bottom 10px
                                font-size $font-size-ms
                                color #aaa
                            .comment-content
                                padding-right 10px
                                padding-bottom 10px
                                line-height 20px
                            .reply
                                padding 15px 15px 15px 0
                                &:first-of-type
                                    margin-top 10px
                                .reply-name
                                    color #3674B3
                                    margin-right 4px
                    .more
                        position absolute
                        background #fff
                        border-radius 4px
                        right 10px
                        top 30px

        .dy-bottom-wrapper
            position fixed
            bottom 0
            width 100%
            height 44px
            .button-wrapper
                display flex
                width 100%
                height 100%
                background-color #fff
                box-shadow 0 -1px 6px -1px rgba(200, 200, 200, 0.33)
                overflow hidden
                .line
                    position absolute
                    left 50%
                    top 50%
                    width 1px
                    height 22px
                    transform translate3d(-50%, -50%, 0)
                .item
                    position relative
                    flex 1
                    display flex
                    justify-content center
                    align-items center
                    height 46px
                    &.my-like
                        color #2196f3
            .input-box-wrapper
                width 100%
                height 100%
                background-color #fff
                box-shadow 0 -1px 6px -1px rgba(200, 200, 200, 0.33)
                overflow hidden
                > input
                    width 100%
                    height 46px
</style>



